import React, { Component } from 'react';
import CardList from '../components/CardList';
import BarraBusqueda from '../components/BarraBusqueda';
import { personajes } from '../personajes';

class App extends Component {
	constructor() {
		super();
		this.state = {
			personajes: personajes,
			campobusqueda: ''
		}
	}

	onSearchChange = (event) => {
		this.setState({ campobusqueda: event.target.value });
	}

	render() {
		const { personajes, campobusqueda } = this.state;
		const pjFiltrados = personajes.filter(personaje => {
			return personaje.nombre.toLowerCase().includes(campobusqueda.toLowerCase());
		});
		return (
			<div className='tc'>
				<h1 className='f1'>Personajes</h1>
				<BarraBusqueda searchChange={this.onSearchChange}/>
				<CardList personajes={pjFiltrados}/>
			</div>
		);
	}
}

export default App;