import React from 'react';
import Card from './Card';

const CardList = ({ personajes }) => {
	const cardArray = personajes.map((user, i) => {
		return (
			<Card 
				key={i} 
				imagen={personajes[i].imagen} 
				nombre={personajes[i].nombre} 
				anime={personajes[i].anime}
				url={personajes[i].url}
			/>
		);
	});
	return (
		<div>
			{cardArray}
		</div>
	);
}

export default CardList;