import React from 'react';
import './Card.css';

const Card = ({ nombre, anime, imagen, url }) => {
	return (
		<a href={url} target='_blank' rel='noopener noreferrer'><div className='tc bg-near-white dib br3 pa3 ma2 grow bw2 shadow-5 tarjeta'>
			<img alt='imagen' src={require(`../img/${imagen}.jpg`)} />
			<div>
				<h2>{nombre}</h2>
				<p>{anime}</p>
			</div>
		</div></a>
	);
}

export default Card;